<template>
  <div>
    <headers v-bind:header-name="header"
             v-bind:category-i-d="this.$route.params.categoryId"></headers>
    <div style="margin: 20px;">
      <step step2=true></step>
    </div>
    <div class="conversation-box">
      <div class="conversation-wrapper" v-for="counter in 10" v-bind:key="counter">
        <div class="conversation-container-A"
             v-if="counter % 2 !== 0 && conversation['korean' + counter]">
          <div>
            <img class="conversation-image" :src="conversation.image1">
          </div>
          <div class="conversation-A">
            <p>{{ conversation.chinese_c1 }}</p>
            <img class="conversation-play-btn" src="../../assets/video.png"
                 @click.prevent="playAudio(conversation.audio1)">
          </div>
        </div>
        <div class="conversation-container-B"
             v-if="counter % 2 === 0 && conversation['korean' + counter]">
          <p style="margin-bottom: 5px;" v-show="!conversation.hidden2">
            {{ conversation.chinese_c2 }}
          </p>
          <p style="margin-bottom: 5px;" v-show="conversation.hidden2">
            {{ conversation.chinese_c2_hidden }}
          </p>
          <p>다음 문장을 완성해서 녹음해주세요.</p>
          <div class="conversation-btn-wrapper">
            <img src="../../assets/voicerec.png" v-if="!hidden" @click="record">
            <img src="../../assets/pause.png" v-if="hidden" @click="stop">
            <img src="../../assets/video.png"
            @click.prevent="play">
            <img src="../../assets/show.png" @click="conversation.hidden2 = false">
          </div>
        </div>
      </div>
    </div>
    <pre>{{ mediaRec }}</pre>
  </div>
</template>

<script>
import Vue from 'vue';
import axios from 'axios';
import Headers from '../Header_back';
import Step from './Step_selector';

export default {
  components: {
    Headers,
    Step,
  },
  data() {
    return {
      header: '你好!',
      conversation: {},
      hidden: true,
      mediaRec: '',
    };
  },
  created() {
    const currentdate = new Date();
    const datetime = currentdate.getFullYear().toString()
      + (currentdate.getMonth() + 1).toString()
      + currentdate.getDate().toString()
      + currentdate.getHours().toString()
      + currentdate.getMinutes().toString()
      + currentdate.getSeconds().toString();
    this.mediaRec = new Vue.cordova.Media(`${datetime}.mp3`);

    axios.get(`conversations/${this.$route.params.categoryId}/${this.$route.params.conversationId}/step1`, {
    }).then((response) => {
      this.conversation = response.data;
      const textLength = this.conversation.chinese_c2.length;
      let hiddenText = '';
      for (let i = 0; i <= textLength / 2; i += 1) {
        hiddenText = hiddenText.concat('-');
      }
      this.conversation.chinese_c2_hidden
        = this.conversation.chinese_c2.substr(0, (textLength / 2) - 1) + hiddenText;
      this.conversation.hidden2 = true;
    });
  },
  methods: {
    playAudio(sound) {
      if (sound) {
        const audio = new Audio(sound);
        audio.play();
      }
    },
    showText() {
      this.conversation.hidden2 = false;
      // eslint-disable-next-line
      console.log(this.conversation.hidden2);
    },
    record() {
      this.hidden = true;
      this.mediaRec.startRecord();
    },
    stop() {
      this.mediaRec.stopRecord();
    },
    play() {
      this.mediaRec.play();
    },
    release() {
      this.mediaRec.release();
    },
  },
};
</script>

<style scoped>
  .conversation-box {
    width: 90%;
    margin: auto;
  }

  .conversation-wrapper {
    width: 100%;
  }

  .conversation-wrapper p {
    margin: 0;
  }

  .conversation-container-A {
    display: grid;
    grid-template-columns: 20% 80%;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .conversation-image {
    width: 50px;
  }

  .conversation-A {
    background-color: #e3e3e3;
    border-radius: 15px;
    margin: 5px;
    text-align: left;
    padding: 10px;
    min-height: 60px;
    position: relative;
  }

  .conversation-play-btn {
    width: 25px;
    position: absolute;
    right: 10px;
    bottom: 10px;
  }

  .conversation-container-B {
    border: 1px solid #c5c5c5;
    border-radius: 15px;
    min-height: 60px;
    margin: 5px;
    text-align: left;
    padding: 10px;
    position: relative;
  }

  .conversation-btn-wrapper {
    position: absolute;
    bottom: 10px;
    right: 10px;
  }

  .conversation-btn-wrapper img {
    width: 25px;
  }
</style>



